"use client";

import { useState, useEffect, useRef } from "react";

type Role = "user" | "assistant";

type Message = {
  role: Role;
  content: string;
};

export const Assistant = () =>{
  const [messages, setMessages] = useState<Message[]>([]);
  const [input, setInput] = useState("");
  const [loading, setLoading] = useState(false);
  const chatEndRef = useRef<HTMLDivElement | null>(null);

  useEffect(() => {
    chatEndRef.current?.scrollIntoView({ behavior: "smooth" });
  }, [messages]);

  const handleSend = async () => {
    if (!input.trim()) return;

    const userMsg: Message = { role: "user", content: input };
    const updatedMessages = [...messages, userMsg];
    setMessages(updatedMessages);
    setInput("");
    setLoading(true);

    try {
      const response = await fetch("/api/chat", {
        method: "POST",
        body: JSON.stringify({ messages: updatedMessages }),
      });

      const reader = response.body?.getReader();
      const decoder = new TextDecoder();
      let botMsg: Message = { role: "assistant", content: "" };

      // Add empty assistant message to show streaming
      setMessages((prev) => [...prev, botMsg]);

      while (true) {
        const { done, value } = await reader!.read();
        if (done) break;

        const chunk = decoder.decode(value, { stream: true });
        botMsg.content += chunk;

        // Update the last assistant message
        setMessages((prev) => {
          const updated = [...prev];
          updated[updated.length - 1] = { ...botMsg };
          return updated;
        });
      }
    } catch (err) {
      console.error("Streaming error:", err);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="min-h-screen bg-gray-100 flex flex-col items-center p-4">
      <div className="w-full max-w-2xl flex flex-col flex-grow bg-white shadow-md rounded-xl overflow-hidden">
        {/* Header */}
        <header className="bg-green-700 text-white text-center py-4">
          <h1 className="text-2xl font-bold">Ethical Tax Bot</h1>
        </header>

        {/* Chat Window */}
        <div className="flex-grow p-4 overflow-y-auto space-y-3">
          {messages.map((msg, i) => (
            <div
              key={i}
              className={`max-w-xs whitespace-pre-wrap p-3 rounded-lg ${
                msg.role === "user"
                  ? "bg-green-100 self-end text-right ml-auto"
                  : "bg-gray-200 self-start"
              }`}
            >
              {msg.content}
            </div>
          ))}
          {loading && (
            <div className="text-sm text-gray-500">Waiting for response...</div>
          )}
          <div ref={chatEndRef} />
        </div>

        {/* Input Area */}
        <div className="border-t p-3 flex items-center gap-2">
          <input
            type="text"
            value={input}
            onChange={(e) => setInput(e.target.value)}
            onKeyDown={(e) => e.key === "Enter" && handleSend()}
            placeholder="Ask a tax question..."
            className="flex-grow px-4 py-2 border rounded-full focus:outline-none focus:ring focus:ring-green-300"
          />
          <button
            onClick={handleSend}
            disabled={loading}
            className="bg-green-600 text-white px-4 py-2 rounded-full hover:bg-green-700 transition"
          >
            Send
          </button>
        </div>
      </div>
    </div>
  );
}
